.LeaderboardGallery {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}
.LeaderboardGallery--content {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  pointer-events: visible;
  padding: 12px 12px 24px 12px;
  border-radius: 10px;
  width: calc(100% - 24px);
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}
.LeaderboardGallery--content .Card {
  height: 100%;
}
.LeaderboardGallery--content .Card__in {
  height: 100%;
}
.LeaderboardGallery--card {
  height: auto;
}
.LeaderboardGallery .Gallery__slide {
  background: var(--modal_card_background);
}
